import React, { useState, Component, useEffect } from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
  useHistory,
  Link,
  useRouteMatch,
  useParams
} from "react-router-dom";
import './index.scss';
const appconfig = require('../../appconfig').default;
const request = require('../../networkHandler.js');

function Navigation() {
  let LoginSuccess = useState(false);
  let Loader = useState('hide');
  let errorPage = useState('');
  let errorPage1 = useState('');
  let msgError = useState('');
  let body;
  let header;
  let endpoint;
  let history = useHistory();
  let statusTab = useHistory();

  if ((localStorage.getItem("user_email") != undefined) && (localStorage.getItem("user_email") != "undefined") && (localStorage.getItem("user_email") != null) && (localStorage.getItem("user_email") != "null") && (localStorage.getItem("user_email") != "")) {
    //assetHub="state.score - 2" ;

    document.getElementById('root').classList.remove('hide');
    if (localStorage.getItem('pathSetted') != "oTube") {
      localStorage.setItem('pathSetted', "oTube");
      var myWindow = window.open("https://otube.oracle.com/embed/secure/iframe/entryId/1_xpp70qav/uiConfId/43012852/pbc/105282352", "_blank");
      setTimeout(function () { myWindow.close(); }, 7000);
    }
    // history.push('/governance');   /******* Redirection Alternative */

    //  return <Redirect to="/governance" />

  } else {
    Loader = "";


    if (document.cookie && document.cookie.indexOf('ORA_UCM_INFO') != -1) {
      document.getElementById('root').classList.add('hide');

      global.email = (document.cookie).match(/([a-zA-Z0-9._-]+@[a-zA-Z0-9._-]+\.[a-zA-Z0-9_-]+)/gi)[0];
      localStorage.setItem("user_email", ((global.email).toLowerCase()));
      if ((localStorage.getItem("user_email") != undefined) && (localStorage.getItem("user_email") != "undefined") && (localStorage.getItem("user_email") != null) && (localStorage.getItem("user_email") != "null") && (localStorage.getItem("user_email") != "")) { }

    } else {
      document.getElementById('root').classList.add('hide');
      /* https://run.mocky.io/v3/3eb7001a-ba7c-45c6-b339-854bda8e3f94 ==== ANgan admin/ winloader
         https://run.mocky.io/v3/156abd5c-7046-478c-8e7d-e11354682b9d ==== deepika admin/ winloader
         https://run.mocky.io/v3/993db4c5-4b46-4f56-9e5f-0f0faa9a2a03 === IDRISH reviewer
      global.baseDom + '/ssoheader.php'
      
      */

      //endpoint ='https://run.mocky.io/v3/156abd5c-7046-478c-8e7d-e11354682b9d';
      endpoint = appconfig.REACT_SSO_URL
      header = {};
      request.getIDCSdata(endpoint, header, true).then(idcsLogindata => {
        console.log(idcsLogindata);
        if (idcsLogindata.data.indexOf('<!DOCTYPE html>') == -1) {
          var split = idcsLogindata.data.split('#');
          if ((split[1] != "") && (split[1] != undefined) && (split[1] != null)) {
            global.email = split[0].trim();
            global.name = split[1].trim();
          } else { }
          localStorage.setItem("user_name", global.name);
          localStorage.setItem("user_email", ((global.email).toLowerCase()));

          if ((localStorage.getItem("user_email") != undefined) && (localStorage.getItem("user_email") != "undefined") && (localStorage.getItem("user_email") != null) && (localStorage.getItem("user_email") != "null") && (localStorage.getItem("user_email") != "")) {

            var body = {
              "user_email": localStorage.getItem("user_email")
            }
            var header = {
              "user_email": localStorage.getItem("user_email")

            }
            var endpoint = "/user/findbyemail/";
            request.getProfileByEmailId(endpoint, header, body).then(profiledata => {
              console.log(profiledata.data);
              if (profiledata.data.exist == 'no') {
                localStorage.setItem('loginStatus', profiledata.data.exist);
                global.loginStatus = profiledata.data.exist;

                errorPage = true;
                msgError = 'user is not registered';

                /********* */

              } else {

                localStorage.setItem('loginStatus', profiledata.data.exist);

                global.loginStatus = profiledata.data.exist;
                localStorage.setItem("role", profiledata.data.role);
                localStorage.setItem("location", profiledata.data.location);
                localStorage.setItem("pillar", profiledata.data.pillar);
                localStorage.setItem("phone", profiledata.data.phone);
                localStorage.setItem("leader", profiledata.data.leader);
                localStorage.setItem('user_name', profiledata.data.name)

                if (idcsLogindata.data.lob == null) {
                  localStorage.setItem("LOB", "Others");


                } else {
                  localStorage.setItem("LOB", profiledata.data.lob);

                }
                LoginSuccess = true;
                window.location.href = "";
                // history.push('/governance'); 
                // history.push('/governance');

                /******* */
              }

              /************************Capture login */
              endpoint = "/user/captureactivity/w/login/" + localStorage.getItem("user_name") + '/' + localStorage.getItem("user_email");
              var body = {
                "user_email": localStorage.getItem("user_email")
              }
              var header = {
                "user_email": localStorage.getItem("user_email")

              }
              request.captureLogin(endpoint, header, body).then(profiledata => {
                setTimeout(function () {
                  if (document.getElementById('F-session')) {
                    document.getElementById('F-session').value = localStorage.getItem("user_email");
                    if (document.getElementById('F-session').value != "") {
                      // alert(document.getElementById('F-session').value);
                      //alert('test');
                      window.location.href = window.location.href;
                    }
                  }
                }, 200);

              }).catch(error => {

                console.log("i am in catch" + error);
                localStorage.clear();
                errorPage = true;
                msgError = error;

              });

              /************************Capture login */

            }).catch(error => {

              console.log("i am in catch" + error);
              localStorage.clear();

              errorPage = true;
              msgError = error;

            });
          }
        }

        else {
          //  alert("Bad session, Sorry for the delayed response");
          localStorage.clear();
          errorPage1 = true;
          msgError = 'invalid IDCS data';
          /******** */

        }


      }).catch(error => {

        console.log("i am in catch" + error);
        localStorage.clear();
        errorPage = true;
        msgError = error;
        /**************** */


      });

    }
    Loader = "hide";

  }


  return (
    <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
      <div className="container">
        <a className="navbar-brand" href="/"><img height="30px" src={process.env.PUBLIC_URL + '/images/AssetHublogo.png'} /></a>
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav mr-auto">
            {(localStorage.getItem('role') != null && localStorage.getItem('role') != undefined && localStorage.getItem('role') != "") && <>
              {(localStorage.getItem('role').indexOf('reviewer') != -1 || localStorage.getItem('role').indexOf('admin') != -1) &&
                <li className="nav-item">
                  <Link className={global.statusTab == 'governance' ? "nav-link active" : "nav-link"} to="/governance">Governance</Link>
                </li>
              }

              {localStorage.getItem('leader') == "true" &&
                <li className="nav-item">
                  <Link className={global.statusTab == 'reports' ? "nav-link active" : "nav-link"} to="/reports">Reports</Link>
                </li>
              }
              <li className="nav-item">
                <Link className={global.statusTab == 'myassets' ? "nav-link active" : "nav-link"} to="/myassets">My Assets</Link>
              </li>
              {/* {localStorage.getItem('role').indexOf('winloader') != -1 &&
                    <li className="nav-item">
                      <Link  className={global.statusTab == 'mywins' ? "nav-link active" : "nav-link"}  to="/mywins">My Wins</Link>
                    </li>
                  } */}
            </>}
          </ul>

        </div>

      </div>
    </nav>
  );
}

export default Navigation;