import React, {
    useState,
    Component
} from 'react';

import './index.scss';
import {
    Tabs,
    Tab,
    Table,
    Button,
    Image
} from 'react-bootstrap';
import {
    BrowserRouter as Router,
    Route,
    Link,
    Redirect,
    withRouter
} from "react-router-dom";
const request = require('../../networkHandler.js');

export class Governance extends Component {
    
    
    state = {
        AssetDataLive: [],
        AssetUnderReview: [],
        PendingRectification: [],
        AssetRejected: [],
        AssetLive: [],

        redirectPage: '',
        redirectDetails: '',
        AssetID: '',
        redirectEditPage: '',
        classDeploy: '',
        redirectDeployPage: '',
        redirectDetailPage: '',
        governance: 'N',
        Loader: ""
    }
    constructor(props) {
        super(props);
        this.Loader = "show";
      }
    componentDidMount() {
        localStorage.setItem('selectedlist', "");
        localStorage.setItem('selectedQues', "");
        localStorage.setItem('LVL', "");
        localStorage.setItem('status', "");
        this.setState({
            Loader:"show"
        });
       // alert(this.state.Loader);
        if ((localStorage.getItem("role") != undefined) && (localStorage.getItem("role") != null) && (localStorage.getItem("role") != "")) {

            let role = "/" + localStorage.getItem("role");
            let headers = {
                "user_email": localStorage.getItem("user_email")

            }
            let endpoint = '/governance/assets/' + localStorage.getItem("user_email") + role;
            request.getGovernanceData(endpoint, headers, false).then(governancedata => {

                for (var i = 0; i < governancedata.data.length; i++) {
                    if (governancedata.data[i].status == "Pending Review") {
                        for (var j = 0; j < governancedata.data[i].list.length; j++) {
                            if (governancedata.data[i].list[j].ASSET_REVIEW_NOTE == null) {
                                governancedata.data[i].list[j].ASSET_REVIEW_NOTE = [];
                            }
                        }
                        const AssetUnderReview = governancedata.data[i].list;

                        this.setState({
                            AssetUnderReview
                        });

                    }
                    if (governancedata.data[i].status == "Pending Rectification") {

                        for (var j = 0; j < governancedata.data[i].list.length; j++) {
                            if (governancedata.data[i].list[j].ASSET_REVIEW_NOTE == null) {
                                governancedata.data[i].list[j].ASSET_REVIEW_NOTE = [];
                            }
                        }
                        const PendingRectification = governancedata.data[i].list;
                        this.setState({
                            PendingRectification
                        });

                    }
                    if (governancedata.data[i].status == "Reject") {
                        for (var j = 0; j < governancedata.data[i].list.length; j++) {
                            if (governancedata.data[i].list[j].ASSET_REVIEW_NOTE == null) {
                                governancedata.data[i].list[j].ASSET_REVIEW_NOTE = [];
                            }
                        }
                        const AssetRejected = governancedata.data[i].list;
                        this.setState({
                            AssetRejected
                        });

                    }
                    if (governancedata.data[i].status == "Live") {
                        for (var j = 0; j < governancedata.data[i].list.length; j++) {
                            if (governancedata.data[i].list[j].ASSET_REVIEW_NOTE == null) {
                                governancedata.data[i].list[j].ASSET_REVIEW_NOTE = [];
                            }
                        }
                        const AssetLive = governancedata.data[i].list;
                        this.setState({
                            AssetLive
                        });

                    }

                }
                //document.getElementById('LoaderP').classList.add('hide');

            }).catch(error => {

                console.log("i am in catch" + error);
                //localStorage.clear();
            });
            this.setState({
                Loader:"hide"
            });
            }
    }
    handleViewNote = id => event => {
        var val = document.getElementById('NotesVal' + id).innerHTML;

        if (val == " " || val == "") {
            global.OverviewTab = undefined;
            global.DetailsTab = undefined;
            global.CollateralTab = undefined;
        } else {
            var splitSection = val.split('next');

            for (var i = 0; i < splitSection.length - 1; i++) {
                splitSection[i].split('Title');
                console.log(splitSection[i].split('Title')[1]);
                if (splitSection[i].split('Title')[1] == "Overview") {
                    global.OverviewTab = splitSection[i].split('Title')[0];
                }
                if (splitSection[i].split('Title')[1] == "Architecture") {
                    global.DetailsTab = splitSection[i].split('Title')[0];
                }
                if (splitSection[i].split('Title')[1] == "Collateral") {
                    global.CollateralTab = splitSection[i].split('Title')[0];
                }
                
            }

        }
        this.setState({
            handleViewNote: true,
            AssetID: id
        });

        global.Governance = 'Viewnote'
    }
    redirectDetails = id => event => {
        this.setState({
            redirectDetails: true,
            AssetID: id
        });
    }
    handleDetails = id => event => {
        this.setState({
            redirectDetailPage: true,
            AssetID: id
        });
    }
    handleEdit = (type, id) => event => {
        if (type == "PendingRectification") {
            var result = this.state.PendingRectification.filter(element => ((element.ASSET_ID == id)));
            var jsonP = JSON.parse(result[0].ASSET_REVIEW_NOTE);
            if (jsonP.note != "" && jsonP.note != null && jsonP.note != "undefined") {

                localStorage.setItem('selectedlist', jsonP.note);

            } else {
                localStorage.setItem('selectedlist', "");

            }
        }

        this.setState({
            redirectEditPage: true,
            governance: "Y",
            EditAssetID: id
        });
    }
    handleView = (type, id) => event => {
        if (type == "PendingRectification") {
            var result = this.state.PendingRectification.filter(element => ((element.ASSET_ID == id)));
            localStorage.setItem('checklist', result[0].CHECKLIST_ITEMS);
            var jsonP = JSON.parse(result[0].ASSET_REVIEW_NOTE);

            if (jsonP.note != "" && jsonP.note != null && jsonP.note != "undefined") {

                localStorage.setItem('selectedlist', jsonP.note);

            } else {
                localStorage.setItem('selectedlist', "");

            }
            localStorage.setItem('status', "Pending Rectification");

            localStorage.setItem('selectedQues', jsonP.questions);

            localStorage.setItem('LVL', result[0].ASSET_APPROVAL_LVL)

            console.log(result[0].CHECKLIST_ITEMS);
        }
        if (type == "AssetRejected") {
            var result = this.state.AssetRejected.filter(element => ((element.ASSET_ID == id)));
            localStorage.setItem('checklist', result[0].CHECKLIST_ITEMS);
            var jsonP = JSON.parse(result[0].ASSET_REVIEW_NOTE);

            if (jsonP.note != "" && jsonP.note != null && jsonP.note != "undefined") {
                localStorage.setItem('selectedlist', jsonP.note);

            } else {
                localStorage.setItem('selectedlist', "");
            }
            localStorage.setItem('selectedQues', jsonP.questions);
            localStorage.setItem('status', "Reject");

            localStorage.setItem('LVL', result[0].ASSET_APPROVAL_LVL)

            console.log(result[0].CHECKLIST_ITEMS);
        }
        if (type == "AssetUnderReview") {
            var result = this.state.AssetUnderReview.filter(element => ((element.ASSET_ID == id)));
            localStorage.setItem('checklist', result[0].CHECKLIST_ITEMS);

            var jsonParse = result[0].ASSET_REVIEW_NOTE;
            if (jsonParse != "") {
                if (result[0].ASSET_APPROVAL_LVL == 2) {
                    var jsonP = JSON.parse(result[0].ASSET_REVIEW_NOTE);

                } else {
                    var jsonP = result[0].ASSET_REVIEW_NOTE;

                }

            }
            if (jsonP.note != "" && jsonP.note != null && jsonP.note != undefined) {
                localStorage.setItem('selectedlist', jsonP.note);


            } else {
                localStorage.setItem('selectedlist', "");
            }
            localStorage.setItem('status', "");
            localStorage.setItem('selectedQues', jsonP.questions);
            localStorage.setItem('LVL', result[0].ASSET_APPROVAL_LVL)

        }
        global.Governance = 'Viewnote'
        this.setState({
            redirectPage: true,
            AssetID: id,
            TabStatus: 'Details'
        });
    }
    render() {
            if (this.state.redirectDetails) {
                return <Redirect push to = {
                    "/details/?" + this.state.AssetID
                }
                />; 
            }
            if (this.state.redirectEditPage) {
                return <Redirect push to = {
                    "/create?" + this.state.EditAssetID + "&governanceTab=Y"
                }
                />; 
            }
            if (this.state.handleViewNote) {
                return <Redirect push to = {
                    "/details/?" + this.state.AssetID + "&MyASSET=Y"
                }
                />; 
            }
            if (this.state.redirectPage) {
                return <Redirect push to = {
                    "/details/?" + this.state.AssetID + "&Governance=Y"
                }
                />; 
            }
            if (this.state.redirectDetailPage) {
                return <Redirect push to = {
                    "/details/?" + this.state.AssetID + "&MyASSET=Y"
                }
                />; 
            }
            var AssetUnderReviewnew;
            var PendingRectificationnew;
            var AssetRejectednew;
            var AssetLivenew;

            AssetUnderReviewnew = this.state.AssetUnderReview;
            PendingRectificationnew = this.state.PendingRectification;
            AssetRejectednew = this.state.AssetRejected;
            AssetLivenew = this.state.AssetLive;

            var UnderReviewCnt;
            if (AssetUnderReviewnew.length > 0) {
                UnderReviewCnt = "Pending Review (" + AssetUnderReviewnew.length + ")";
            } else {
                UnderReviewCnt = "Pending Review";
            }
            var PendingRectificationCnt;
            if (PendingRectificationnew.length > 0) {
                PendingRectificationCnt = "Requires Rectification (" + PendingRectificationnew.length + ")";
            } else {
                PendingRectificationCnt = "Requires Rectification";
            }
            var AssetRejectednewCnt;
            if (AssetRejectednew.length > 0) {
                AssetRejectednewCnt = "Rejected (" + AssetRejectednew.length + ")";
            } else {
                AssetRejectednewCnt = "Rejected";
            }

            var AssetLivenewCnt;
            if (AssetLivenew.length > 0) {
                AssetLivenewCnt = "Live (" + AssetLivenew.length + ")";
            } else {
                AssetLivenewCnt = "Live";
            }





            return ( <div className="mb-5">
                        <Tabs defaultActiveKey="pendingReview" id="uncontrolled-tab-example">
                            <Tab eventKey="pendingReview" title={UnderReviewCnt}>
                                <div class="pt-5">
                                    <Table borderless>
                                    {AssetUnderReviewnew.length>0 && <>
                                    <thead>
                                        <tr>
                                            <th>Customer</th>
                                            <th>Title</th>
                                            <th>Asset ID</th>
                                            <th>Asset Type</th>
                                            <th>Asset Owner</th>
                                            <th>Action</th>
                                        </tr>
                                    </thead>
                                    {AssetUnderReviewnew.map((AssetUnderReview,index)=> 
                                    <tbody>
                                        <tr>
                                            <td width="142">
                                                <Image src={AssetUnderReview.ASSET_THUMBNAIL}  thumbnail  />
                                            </td>
                                            <td>{AssetUnderReview.ASSET_TITLE}</td>
                                            <td>{AssetUnderReview.ASSET_ID}</td>
                                            <td>{AssetUnderReview.ASSET_TYPE}</td>
                                            <td>{AssetUnderReview.ASSET_OWNER.split('\n').map((item, key) => {
                                                return <span key={key}>{item}<br/></span>
                                                })}
                                            </td>
                                            <td>
                                                <p className="mb-2">
                                                    <Button variant="danger" size="sm" block onClick={this.handleView('AssetUnderReview',AssetUnderReview.ASSET_ID)}>Review</Button> 
                                                </p>
                                                <Button variant="danger" size="sm" block onClick={this.handleEdit("AssetUnderReview",AssetUnderReview.ASSET_ID)}>Edit</Button>
                                            </td>   
                                        </tr>
                                    </tbody>
                                    )}
                                    </>}{AssetUnderReviewnew.length<=0 && 
                                    <p class="text-center p15">
                                        No Records Found.
                                    </p>
                                    }
                                </Table>
                                </div>
                            </Tab>
                            <Tab eventKey="rectificationRequested" title={PendingRectificationCnt}>
                                <div class="pt-5">
                                    <Table borderless>
                                    {PendingRectificationnew.length>0 && <>
                                    <thead>
                                        <tr>
                                        <th>Customer</th>
                                        <th>Title</th>
                                        <th>Asset ID</th>
                                        <th>Asset Type</th>
                                        <th>Asset Owner</th>
                                        <th>Action</th>
                                        </tr>
                                    </thead>
                                    {PendingRectificationnew.map((PendingRectification,index)=> 
                                    <tbody>
                                        <tr>
                                        <td width="142">
                                            <Image src={PendingRectification.ASSET_THUMBNAIL}  thumbnail  />
                                        </td>
                                        <td>{PendingRectification.ASSET_TITLE}</td>
                                        <td>{PendingRectification.ASSET_ID}</td>
                                        <td>{PendingRectification.ASSET_TYPE}</td>
                                        <td>{PendingRectification.ASSET_OWNER.split('\n').map((item, key) => {
                                            return <span key={key}>{item}<br/></span>
                                            })}
                                        </td>
                                        <td>
                                            <p className="mb-2">
                                                <Button variant="danger" size="sm" block onClick={this.handleView('PendingRectification',PendingRectification.ASSET_ID)}>Review</Button> 
                                            </p>
                                            <Button variant="danger" size="sm" block onClick={this.handleEdit("PendingRectification",PendingRectification.ASSET_ID)}>Edit</Button>
                                        </td>
                                        </tr>
                                    </tbody>
                                    )}
                                    </>}{PendingRectificationnew.length<=0 && 
                                    <p class="text-center p15">
                                        No Records Found.
                                    </p>
                                    }
                                </Table>
                                </div>
                            </Tab>
                            <Tab eventKey="rejected" title={AssetRejectednewCnt}>
                                <div class="pt-5">
                                    <Table borderless>
                                    {AssetRejectednew.length>0 && <>
                                    <thead>
                                        <tr>
                                        <th>Customer</th>
                                        <th>Title</th>
                                        <th>Asset ID</th>
                                        <th>Asset Type</th>
                                        <th>Asset Owner</th>
                                        <th>Action</th>
                                        </tr>
                                    </thead>
                                    {AssetRejectednew.map((AssetRejected,index)=> 
                                    <tbody>
                                        <tr>
                                        <td width="142">
                                            <Image src={AssetRejected.ASSET_THUMBNAIL}  thumbnail />
                                        </td>
                                        <td>{AssetRejected.ASSET_TITLE}</td>
                                        <td>{AssetRejected.ASSET_ID}</td>
                                        <td>{AssetRejected.ASSET_TYPE}</td>
                                        <td>{AssetRejected.ASSET_OWNER.split('\n').map((item, key) => {
                                            return <span key={key}>{item}<br/></span>
                                            })}
                                        </td>
                                        <td>
                                            <p className="mb-2">
                                                <Button variant="danger" size="sm" block onClick={this.handleView('AssetRejected',AssetRejected.ASSET_ID)}>Review</Button> 
                                            </p>
                                            <Button variant="danger" size="sm" block onClick={this.handleEdit("AssetRejected",AssetRejected.ASSET_ID)}>Edit</Button>
                                        </td>
                                        </tr>
                                    </tbody>
                                    )}
                                    </>}{AssetRejectednew.length<=0 && 
                                    <p class="text-center p15">
                                        No Records Found.
                                    </p>
                                    }
                                </Table>
                                </div>
                            </Tab>
                        </Tabs>
                        </div>)
                        }
                    }

                    export default Governance