import React, { useState, Component} from 'react';
import './index.scss';
import {Form, Card,Button} from 'react-bootstrap';
const request = require('../../networkHandler.js');
class ParentReviewSec extends Component {
    constructor(props) {
        super(props);
        this.state = {
            invalidUrl: '',
            ReviewNoteOverviewVal: "",
            checklist: [],
            ReviewNoteOverviewVal: "",
            selectedQues: [],
            ReviewStatus: ""
        };
    }
    componentDidMount() {

        var url = window.location.href;
        var statusGov = url.split('&Governance=');
        var MyAssetStat = url.split('&MyASSET=');

        if ((statusGov[1] == 'Y') || MyAssetStat[1] == 'Y') {
            var selectedreview = localStorage.getItem('selectedlist');

            if (statusGov[1] == 'Y') {
                var selectedObject = localStorage.getItem('selectedQues');
                var retrievedObject = JSON.parse(localStorage.getItem('checklist'));
                var li = "";

                for (var i = 0; i < retrievedObject.length; i++) {
                    li += '<li><input type="checkbox" name="questionnaire" id=' + retrievedObject[i].id + ' value=' + retrievedObject[i].value + '/> ' + retrievedObject[i].value + '</li>';
                }

                document.getElementById('checklist').innerHTML = li;
                if (selectedObject != "") {
                    var arrselected = selectedObject.split(',');
                    for (var i = 0; i < arrselected.length; i++) {
                        var id = arrselected[i];
                        // alert(document.getElementById(id))
                        if (document.getElementById(id)) {
                            document.getElementById(id).checked = true;

                        }
                    }
                }
                this.setState({
                    checklist: retrievedObject,
                    ReviewNoteOverviewVal: selectedreview

                });

            } else {
                this.setState({
                    ReviewNoteOverviewVal: selectedreview,

                });
            }



            if (localStorage.getItem('status') != "" && localStorage.getItem('status') != undefined && localStorage.getItem('status') != null) {
                this.setState({
                    ReviewStatus: localStorage.getItem('status')
                });
            }


            this.setState({
                reviewClass: ' rightNav rightSection'
            });

        } else if (MyAssetStat[1] == 'N') {
            this.setState({
                reviewClass: 'hide rightNav rightSection'
            });
        } else {
            this.setState({
                reviewClass: 'hide rightNav rightSection'
            });


        }

    }
    handleChange = (e) => {
        this.setState({
            [e.target.name]: e.target.value
        })
    }

    reviewAsset = (val) => {
        var markedCheckbox = document.querySelectorAll('input[name="questionnaire"]:checked');
        var selectedQues = [];
        for (var checkbox of markedCheckbox) {
            selectedQues.push(checkbox.id);

        }
        if (this.state.ReviewStatus == "") {
            alert('Please select status');
            return false;
        } else if (this.state.ReviewNoteOverviewVal == "") {
            alert('Please enter Message');
            return false;
        } else {
            var txt;
            var statusmsg;
            if (this.state.ReviewStatus == 'Pending Rectification') {
                statusmsg = 'sent for rectification';
            } else if (this.state.ReviewStatus == 'Live') {
                statusmsg = 'approved';
            } else {
                statusmsg = this.state.ReviewStatus;
            }
            var r = window.confirm("Are you sure you want to change the asset status to " + statusmsg);
            if (r == true) {
                var data = {
                    "note": this.state.ReviewNoteOverviewVal,
                    "questions": selectedQues
                }
                // objJson.push(data);
                var levelOfapprover;
                if (this.state.ReviewStatus == 'Live') {
                    if (localStorage.getItem('LVL') == 1) {
                        levelOfapprover = 2;

                    } else {
                        levelOfapprover = 0; ////Live

                    }
                } else {
                    levelOfapprover = localStorage.getItem('LVL');
                }
                


                var body = {
                    "review_note": data,
                    "assetId": global.ID,
                    "asset_status": this.state.ReviewStatus,
                    asset_status_lvl: levelOfapprover,
                    "user_email": sessionStorage.getItem("user_email")
                };
        
        
                var header = {
                    "user_email": localStorage.getItem("user_email")
        
                }
                var endpoint = "/governance/postreviewnote";
                request.Reviewnote(endpoint, header, body).then(note => {
                    alert(note.data.status);
                    window.location.href = "/governance"
                    this.setState({
                        RedirectBack: 'true'
                    });
                }).catch(error => {

                    console.log("i am in catch" + error);
    
    
                });

            } else {
                txt = "You pressed Cancel!";
            }




        }


    }
    render() {return (  <Card><div className={this.state.reviewClass}>
                                <div className="form-group reviewSec" >
                                    <Card.Header>Review Box</Card.Header>
                                    <Card.Body>
                                    {/* <Form>
                                        <Form.Group controlId="exampleForm.SelectCustom">
                                            <Form.Label>Select a Status</Form.Label>
                                            <Form.Control as="select" custom>
                                                <option>1</option>
                                                <option>2</option>
                                                <option>3</option>
                                                <option>4</option>
                                                <option>5</option>
                                            </Form.Control>
                                        </Form.Group>
                                    </Form> */}
                                    {/* <label for="cars"></label><br/> */}
                                    {this.state.checklist.length!=0 && <>
                                    <label for="cars">Choose a status:</label>
                                    <select name="ReviewStatus" value={this.state.ReviewStatus} id="ReviewStatus" onChange={e =>
                                        this.handleChange(e)}>
                                        <option value="">select status</option>
                                        <option value="Live">Approve</option>
                                        <option value="Pending Rectification">Send for Rectification</option>
                                        <option value="Reject">Reject</option>
                                    </select>
                                    <br/>
                                    </>}
                                    <div class="mt-4 mb-3">
                                        <label class="">Message</label>
                                        <Form.Control as="textarea" id="ReviewNoteOverviewVal" value={this.state.ReviewNoteOverviewVal} name='ReviewNoteOverviewVal' maxLength="4000"
                                            onChange={e => this.handleChange(e)}>
                                        </Form.Control>
                                    </div>
                                    <div id="checkboxes">
                                        {this.state.checklist.length!=0 && 
                                        <label class="">Asset Checklist</label>}
                                        <ul id="checklist">
                                        </ul>
                                        {this.state.checklist.length!=0 && 
                                            <div class="text-center mt-4">
                                                <Button variant="danger" size="lg" onClick={e => this.reviewAsset(e)}>Submit</Button> 
                                            </div>
                                        }
                                    </div>
                                </Card.Body>
                                </div>
                            </div>
                        </Card>);
                        }
                    }

                    export default ParentReviewSec;