
import React, { useState, Component } from 'react';
import { Accordion, Card, Button } from 'react-bootstrap';

import './index.scss';
const request = require('../../networkHandler.js');
export class Filters extends Component {
  constructor(props) {
    super(props);
    // this.handleChange = this.handleChange.bind(this);
    this.state = {
      filters: [],
      filtersData: []
    }
  }
  componentDidMount() {
    var tmp = this.props.FilterData;
    tmp.forEach(function (element) {
      element.CHILD.forEach(function (e) {
        e.Active = false;
      })
    });
    this.setState({
      filtersData: tmp
    })
  }
  FilterToggle = () => event => {
    var tmp = this.props.FilterData;
    tmp.forEach(function (element) {
      element.CHILD.forEach(function (e) {
        e.Active = false;
      })
    });
    this.setState({
      filtersData: tmp
    })
    const element = document.querySelector("#filters-content");
    const containerContent = document.getElementById("container-content");
    //console.log(containerContent);
    if (element.classList.contains("hide")) {
      element.classList.remove('hide');
      containerContent.style.marginLeft = "19%";
      containerContent.style.transition = "all .2s ease";
      containerContent.classList.add('container-content');
    } else {
      element.classList.add('hide');
      containerContent.style.marginLeft = "0%";
      containerContent.classList.remove('container-content');
    }
  }
  selectFilter = (e, id) => {
    var data = this.state.filtersData;
    var selected = this.state.filters;
    // if (e.target.classList.contains('badge-warning')) {
    //   const index = data.indexOf(id);
    //   if (index > -1) {
    //     data.splice(index, 1);
    //   }
    //   e.target.classList.remove('badge-warning');
    //   e.target.classList.add('badge-light');
    // } else {
    //   data.push(id);
    //   e.target.classList.remove('badge-light');
    //   e.target.classList.add('badge-warning');
    // }
    data.forEach(function (element) {
      element.CHILD.forEach(function (e) {
        if (id == e.FILTER_ID) {
          if (e.Active) {
            const index = selected.indexOf(id);
            if (index > -1) {
              selected.splice(index, 1);
            }
          }
          else
            selected.push(id);
          e.Active = !e.Active;
        }
      })
    });
    this.setState({
      filtersData: data,
      filters: selected
    })
    console.log(this.state.filters);
  }
  filter() {
    this.props.callfun(this.state.filters.join(','))
  }
  clearAll(e) {
    var data = this.state.filtersData;
    data.forEach(function (element) {
      element.CHILD.forEach(function (e) {
        if (e.Active)
          e.Active = false;
      })
    });
    this.setState({
      filtersData: data,
      filters: []
    })
    this.setState({
      filtersData: this.props.FilterData,
    })
    this.filter();
  }
  render() {
    const addBorder = {};
    return (
      <div class="filter-cover">
        <a href="javascript:void(0)" class="gray" onClick={this.FilterToggle()} >
          <img width="30px" src="/images/Filters-icon.png" alt="Filters" />
          <span class="ml-2">Filters</span>
        </a>
        <div class="filters-content hide" id="filters-content">
          <Accordion defaultActiveKey="1">
            <Card>
              <div class="h5">Applied Filters <br></br>
                <a style={{ margin: "0 0 0 10px" }} href="javascript:void(0)" onClick={(e) => this.filter(e)} class="float-right font-weight-normal">Apply</a>
                <a class="float-right font-weight-normal" href="javascript:void(0)" onClick={(e) => this.clearAll(e)}>Clear all</a></div>
              {this.state.filtersData.map((item, index) => (
                <div>
                  <Card.Header>
                    <Accordion.Toggle as={Button} variant="link" name={item.FILTER_ID} eventKey={index + 1}>
                      {item.FILTER_NAME}
                    </Accordion.Toggle>
                  </Card.Header>
                  <Accordion.Collapse eventKey={index + 1}>
                    <Card.Body>
                      {item.CHILD.map(key => (
                        <span onClick={(e) => this.selectFilter(e, key.FILTER_ID)} className={"badge badge-pill " + (key.Active ? 'badge-warning' : 'badge-light')} value={key.FILTER_ID} name={key.FILTER_ID}>{key.FILTER_NAME}</span>
                      ))}
                    </Card.Body>
                  </Accordion.Collapse>
                </div>
              ))}

              {/* <Card.Header>
                <Accordion.Toggle as={Button} variant="link" eventKey="1">
                  Asset Type
              </Accordion.Toggle>
              </Card.Header>
              <Accordion.Collapse eventKey="1">
                <Card.Body>
                  Asset Type Content
              </Card.Body>
              </Accordion.Collapse>
              <Card.Header>
                <Accordion.Toggle as={Button} variant="link" eventKey="2">
                  Workload Category
              </Accordion.Toggle>
              </Card.Header>
              <Accordion.Collapse eventKey="2">
                <Card.Body>
                  Workload Category content
              </Card.Body>
              </Accordion.Collapse>
              <Card.Header>
                <Accordion.Toggle as={Button} variant="link" eventKey="3">
                  Workload Type
              </Accordion.Toggle>
              </Card.Header>
              <Accordion.Collapse eventKey="3">
                <Card.Body>
                  Workload Type content
              </Card.Body>
              </Accordion.Collapse>
              <Card.Header>
                <Accordion.Toggle as={Button} variant="link" eventKey="4">
                  Industry
              </Accordion.Toggle>
              </Card.Header>
              <Accordion.Collapse eventKey="4">
                <Card.Body>
                  Industry content
              </Card.Body>
              </Accordion.Collapse> */}
            </Card>
          </Accordion>
        </div>
      </div>
    )
  }
}
export default Filters
